.heading {
  display: flex;
  justify-content: space-between;
  height: 40px; }
  .heading__leftBlock {
    display: flex;
    align-self: center; }
  .heading__text {
    font-size: 28px;
    font-family: TTLakes-Bold, sans-serif;
    line-height: 40px;
    align-self: center; }
    .heading__text:before {
      margin-right: 16px;
      display: block;
      float: left;
      content: "";
      font-size: 16px;
      color: #41F6D7;
      height: 40px;
      width: 4px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      background: #41F6D7; }
    @media screen and (max-width: 550px) {
      .heading__text:before {
        margin-right: 12px; } }
    @media (min-width: 551px) and (max-width: 1119px) {
      .heading__text:before {
        margin-right: 20px; } }
    @media screen and (min-width: 1120px) {
      .heading__text:before {
        margin-right: 30px; } }
  .heading__rightBlock {
    display: flex;
    padding-right: 32px; }
    @media screen and (max-width: 1119px) {
      .heading__rightBlock {
        display: none; } }
  .heading__button {
    margin-left: 32px;
    display: flex;
    justify-content: space-between;
    flex-grow: 1; }
  .heading__textForButton {
    margin-right: 9px;
    font-size: 16px;
    color: #1B1C21;
    align-self: center;
    font-family: TTLakes-Regular, sans-serif;
    cursor: pointer; }
  @media screen and (max-width: 550px) {
    .heading {
      padding-top: 24px;
      padding-bottom: 16px; } }
  @media (min-width: 551px) and (max-width: 1119px) {
    .heading {
      padding-top: 24px;
      padding-bottom: 24px; } }
  @media screen and (min-width: 1120px) {
    .heading {
      padding-top: 42px;
      padding-bottom: 0; } }
