.swiper {
  padding-top: 16px; }
  .swiper .firma {
    width: calc(100vw - 32px);
    height: 100px;
    z-index: 25; }
    @media screen and (min-width: 551px) {
      .swiper .firma {
        display: none; } }
  .swiper .model {
    width: calc(100vw - 32px);
    height: 210px; }
    @media screen and (min-width: 551px) {
      .swiper .model {
        display: none; } }
  .swiper .price {
    width: calc(100vw - 32px);
    height: 260px; }
    @media screen and (min-width: 551px) {
      .swiper .price {
        display: none; } }
  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    line-height: 20px;
    font-size: 14px;
    background-color: #b9b8b8; }
  .swiper-pagination-bullet-active {
    background-color: #B5B6BC; }
