.sidebar {
  z-index: 5;
  display: none;
  flex-direction: column;
  width: 320px;
  min-width: 320px;
  margin-left: 0;
  background-color: #FFFFFF;
  transition: 1s;
  box-shadow: -2px 0 4px rgba(69, 79, 126, 0.02), 16px 0 52px rgba(14, 24, 80, 0.2); }
  .sidebar_open {
    transform: translateX(400px); }
  .sidebar__menu {
    width: 280px;
    display: flex;
    justify-content: space-between;
    box-shadow: none; }
    @media screen and (max-width: 1119px) {
      .sidebar__menu .menu__leftBlock {
        width: 160px; }
      .sidebar__menu .menu__rightBlock {
        width: 40px; } }
    @media screen and (min-width: 1120px) {
      .sidebar__menu .menu__leftBlock {
        width: 120px; }
      .sidebar__menu .menu__rightBlock {
        width: 40px; } }
  .sidebar__main {
    width: 320px;
    height: calc(100vh - 88px);
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  .sidebar__button_active {
    position: relative;
    font-size: 16px;
    padding-left: 0;
    transform: translateX(-26px); }
    .sidebar__button_active:hover {
      box-shadow: none; }
  .sidebar__navigation .navigation__list {
    padding-left: 26px;
    padding-bottom: 5px;
    display: flex;
    width: 320px;
    flex-direction: column;
    overflow: scroll;
    flex-wrap: nowrap; }
    .sidebar__navigation .navigation__list::-webkit-scrollbar {
      width: 0;
      height: 0; }
  .sidebar__footer {
    padding: 0 24px 24px 24px;
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 196px; }
  .sidebar__contactButtons {
    width: 152px;
    display: flex;
    justify-content: space-between; }
  .sidebar__contactEmail {
    font-size: 16px;
    color: #1B1C21;
    font-family: TTLakes-Bold, sans-serif; }
  .sidebar__contactPhone {
    font-size: 24px;
    color: #1B1C21;
    letter-spacing: -0.6px;
    font-family: TTLakes-Bold, sans-serif; }
  .sidebar__footerLang {
    margin: 0;
    padding: 0;
    width: 90px;
    height: 22px;
    font-size: 16px;
    font-family: TTLakes-Regular, sans-serif;
    list-style: none;
    display: flex;
    justify-content: space-between; }
    .sidebar__footerLang_active {
      color: #7E7E82;
      font-size: 16px;
      font-family: TTLakes-Regular, sans-serif; }
    .sidebar__footerLang_inactive {
      color: #1B1C21;
      align-self: center;
      cursor: pointer; }
  @media screen and (max-width: 1119px) {
    .sidebar {
      display: flex;
      position: fixed;
      left: -400px;
      top: 0; } }
  @media screen and (min-width: 1120px) {
    .sidebar {
      display: flex;
      box-shadow: none; } }

@media screen and (min-width: 1120px) {
  .menu__button.button.button_cancel {
    display: none; } }
