.menu {
  min-width: 280px;
  display: flex;
  justify-content: space-between;
  height: 88px;
  left: 0;
  top: 0;
  background: #FFFFFF;
  box-shadow: 0 2px #D9FFF5;
  padding-right: 20px;
  padding-left: 20px; }
  .menu__leftBlock {
    width: 153px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-self: center; }
    @media screen and (max-width: 550px) {
      .menu__leftBlock {
        width: 153px; } }
    @media (min-width: 551px) and (max-width: 1119px) {
      .menu__leftBlock {
        width: 180px; } }
  .menu__rightBlock {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-self: center; }
    @media screen and (max-width: 550px) {
      .menu__rightBlock {
        width: 100px; } }
    @media screen and (min-width: 1120px) {
      .menu__rightBlock {
        width: auto; } }
  .menu__logo {
    height: 50px;
    width: 96px;
    cursor: pointer; }
  .menu__divider {
    width: 2px;
    height: 32px;
    background-color: #EAEAEA;
    border-radius: 1px;
    align-self: center; }
  .menu__button {
    align-self: center; }
  @media screen and (max-width: 550px) {
    .menu {
      justify-content: space-between;
      padding-left: 16px;
      padding-right: 16px; } }
  @media (min-width: 551px) and (max-width: 1119px) {
    .menu {
      padding-left: 20px;
      padding-right: 20px; } }

.hidden-desktop {
  display: flex; }
  @media screen and (min-width: 1120px) {
    .hidden-desktop {
      display: none; } }

@media screen and (max-width: 550px) {
  .hidden-mobile {
    display: flex; } }

@media (min-width: 551px) and (max-width: 1119px) {
  .hidden-mobile {
    display: none; } }

@media screen and (max-width: 550px) {
  .hidden-tablet {
    display: none; } }

@media (min-width: 551px) and (max-width: 1119px) {
  .hidden-tablet {
    display: flex; } }
