@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.woff) format("woff"); }

@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.woff) format("woff"); }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.woff) format("woff"); }

@font-face {
  font-family: "TTLakes-Black";
  src: url(./fonts/TTLakes-Black.woff) format("woff"); }

html {
  overflow-x: hidden;
  overflow-y: auto;
  background-color: white;
  width: 100vw; }

.html_dontOverflow {
  overflow-y: hidden; }

.blur {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #cccccc;
  transition: opacity 2s; }
  .blur_hidden {
    opacity: 0;
    display: none; }
  .blur_openMenu {
    display: block;
    opacity: 0.6;
    z-index: 2; }
  .blur_openPopup {
    display: block;
    opacity: 0.6;
    z-index: 7; }

body {
  max-width: 1440px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  background-color: #F8F8F8; }
  @media screen and (min-width: 1120px) {
    body {
      flex-direction: row; } }

@media screen and (min-width: 1120px) {
  .body-block {
    max-width: calc(1440px - 320px);
    width: calc(100vw - 320px); } }
